import React from 'react';

function Footer() {
    return (
        <footer className="flex">
            <div className="flex g-1">
                <a href="https://twitter.com"><img src="images/vector-1.svg" alt="twitter logo"/></a>
                <a href="https://facebook.com"><img src="images/vector-2.svg" alt="facebook logo"/></a>
                <a href="https://github.com/orientalArg"><img src="images/vector.svg" alt="github logo"/></a>
            </div>
            <p>Café Del Plata 2021 | 2024 </p>
            <p>Diseño y Código<br/><a href="https://github.com/orientalArg" className="author" title="Mis Repositorios">Joaquín Reynoso</a></p>
        </footer>
    )
}

export default Footer; 