import React, { Component } from 'react'

export default class Carrito extends Component {
    render() {
        const {carrito} = this.props;
        return (
            <div className="flex p-3 carrito">
                {carrito.length === 0? (
                <div className="titulo">El carrito está vacio</div>
                ) : (
                <div className="titulo">Tienes {carrito.length} elementos en el carrito</div>
                )}
                <ul Style="padding: 1rem 0">
                    {carrito.map(item =>(
                        <li key={item.id} className="flex" title={item.descripcion}>
                            <div>
                                <img src={item.imagen} alt={item.nombre} />
                            </div>
                            <div>
                                <p className="titulo">{item.nombre}</p>
                            </div>
                            <div Style="color:green;">{item.count} x ${item.precio * item.count}</div>
                            <button onClick={()=>this.props.quitarDelCarrito(item)}>Quitar</button>
                        </li>
                    ))}
                </ul>
                {carrito.length === 0 ? (
                    ''
                ) : (
                    <div>
                        <button>Pagar</button>
                    </div>
                )}
            </div>
        )
    }
}