import React, { Fragment } from 'react'

function Header() {
    return (
        <Fragment>
            <header className="flex">
                <a className="flex g-1 logo" href="/">
                    <p title="PÁGINA DE INICIO">CaféDelPlata</p>
                    <img src="images/LOGO.svg" alt="coffee mug"/>    
                </a>
                <nav className="flex g-1">
                    <a href="/tienda" title="NUESTROS PRODUCTOS">Tienda</a>
                    <p>|</p>
                    <a href="/nosotros#contacto">Encuentranos</a>
                    <p>|</p>
                    <a href="/nosotros">Nosotros</a>
                </nav>
            </header>
        </Fragment>
    )
}

export default Header;