import React, { Component } from 'react';
import Header from './Header.jsx';
import Footer from './Footer.jsx'
import { productos } from './productos.js'
import Carrito from './Carrito.jsx';
import Modal from 'react-modal'

export default class ListaProductos extends Component {
    constructor(props) {
        super(props);
        this.state = {
            producto: null,
            carrito: [],
        };
    }
    quitarDelCarrito = (producto) => {
       const carrito = this.state.carrito.slice();
       this.setState({ carrito:  carrito.filter (x=>x.id !== producto.id)})
    } 
    agregarAlCarrito = (producto) => {
        const carrito = this.state.carrito.slice();
        let alreadyInCart = false;
        carrito.forEach(item => {
            if(item.id === producto.id){
                item.count++;
                alreadyInCart = true;
            }
        });
        if (!alreadyInCart) {
            carrito.push({...producto, count: 1})
        }
        this.setState({carrito})
    }
    abrirModal = (producto) => {
        this.setState({ producto });
    }
    cerrarModal = () => {
        this.setState({ producto: null });
    }
    getProduct(producto, index) {
        return (
            <div key={index} className="flex product">
                <div className="flex left">
                    <img src={producto.imagen} alt={producto.nombre}/>
                    <p>{producto.peso} gramos</p>
                </div>
                <ul className="flex right g-2">
                    <li><p className="precio">$ {producto.precio}</p></li>
                    <li><strong><p>{producto.nombre}</p></strong></li>
                    <li><a href={"#" + producto.id} onClick={() => this.abrirModal(producto)}>Detalles</a></li>
                </ul>
            </div>
        )
    }
    render() {
        const {producto} = this.state;
        return (
            <>
                <Header />
                <Carrito carrito={this.state.carrito} quitarDelCarrito={this.quitarDelCarrito}/>
                <div className="g-1 product-list p-3">
                    {
                    productos.map((producto, index) => {
                        return this.getProduct(producto, index);
                    })
                    }
                </div>
                { producto && (
                    <Modal isOpen={true} >
                        <div className="flex f-width modal">
                            <div className="flex center left">
                                <img src={producto.imagen} alt={producto.nombre}/>
                            </div>
                            <div className="flex right">
                                <button className="cerrar" onClick={this.cerrarModal}><p>x</p></button>
                                <p><strong>{producto.nombre}</strong></p>
                                <div className="flex g-2">
                                    <p className="precio">${producto.precio}</p>
                                    <p>{producto.peso} Gramos</p>
                                </div>
                                <div className="descripcion"><p>{producto.descripcion}</p></div>
                                <div className="flex g-1 botones">
                                    <button >Pagar</button>
                                    <button onClick={() => this.agregarAlCarrito(producto)} >Añadir<img src="/images/cart.svg" alt="shop cart" /></button>
                                </div>
                            </div>
                        </div>
                    </Modal>
                )}
                <Footer />
            </>
        );
    }
}